import { Component, OnInit } from '@angular/core';
import { LayoutService } from "../../services/layout.service";

@Component({
	selector: 'app-activity-home',
	templateUrl: './activity-home.component.html',
	styleUrls: ['./activity-home.component.css']
})
export class ActivityHomeComponent implements OnInit {

	input_param; 
	category_id = 0;
	sortType = 'post_date';
	order = 'desc';
	limit = 2;

	constructor(public layoutService:LayoutService) { }

	ngOnInit() {
		this.layoutService.getSummary('page_activity_introduction');
		this.input_param = {
			website_id: 1,
			category_id: this.category_id,
			sort: this.sortType,
			sort_order: this.order,
			type: 'activity',
			from: 0,
		};
	}


	//sort 一般篩選器
	sort($type,$order){
		this.sortType = $type;
		this.order = $order;
		this.input_param = {
			website_id: 1,
			category_id: this.category_id,
			sort: $type,
			sort_order: $order,
			type: 'activity',
			from: 0,
		};
	}


	//category component output
	getCategoryId($id){
		this.category_id = $id;
		this.input_param = {
			website_id: 1,
			category_id: $id,
			sort: 'post_date',
			sort_order: 'desc',
			type: 'activity',
			from: 0,
		};
		//location.reload();
	}

}
