import { Component, OnInit, Input, SimpleChanges } from '@angular/core';
import { HttpService } from '../../services/http.service';
import { ProductService } from "../../services/product.service";
import  * as Config from '../../models/config.model';

@Component({
	selector: 'app-ebook-list',
	templateUrl: './ebook-list.component.html',
	styleUrls: ['./ebook-list.component.scss']
})
export class EbookListComponent implements OnInit {

	//input 顯示序號
	@Input() input_param;


	//如果input進來的參數改變, 重新取得心理測驗列表
	ngOnChanges(changes: SimpleChanges) {
		console.log(changes);
    	this.input_param = changes.input_param.currentValue;
    	this.readmoreCheck = 0;
    	this.getEbookList();
	}

	
	list;
	fileUrl = Config.FileUrl;
	//是否開啟read more div
	readmoreCheck = 0;
	//一次要顯示幾筆
	limit = 4;
	moredata = 1;

	constructor(private http:HttpService, private productService:ProductService) { }

	ngOnInit() {
		this.readmoreCheck = 0;
		this.getEbookList();
	}


	//看更多
	more() {
		this.readmoreCheck = 1;
		this.input_param.from+=this.limit;	
	}


	//取得文章測驗列表
	getEbookList() {
		this.input_param.limit = this.limit;
		this.productService.getProductList(this.input_param).subscribe(
			res => {
				//沒有資料 隱藏more data區塊
				if(res.products.length==0){
					console.log("empty array");
					this.moredata = 0;
				}

				this.list = res.products;
				console.log(res);
			}
		);

	}


}
