import { Component, OnInit, Input, SimpleChanges } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';

@Component({
	selector: 'app-product-review',
	templateUrl: './product-review.component.html',
	styleUrls: ['./product-review.component.scss']
})
export class ProductReviewComponent implements OnInit {

	//input 顯示序號
	@Input() input_param;


	//如果input進來的參數改變, 重新取得心理測驗列表
	ngOnChanges(changes: SimpleChanges) {
		console.log(changes);
    	this.input_param = changes.input_param.currentValue;
    	this.readmoreCheck = 0;
    	this.getReviewList();
	}

	
	list;
	fileUrl = Config.FileUrl;
	//是否開啟read more div
	readmoreCheck = 0;
	//一次要顯示幾筆
	limit = 2;
	moredata = 1;

	constructor(private httpService:HttpService) { }

	ngOnInit() {
		this.readmoreCheck = 0;
		this.getReviewList();
	}


	//看更多
	more() {
		this.readmoreCheck = 1;
		this.input_param.from+=this.limit;	
	}


	//取得文章測驗列表
	getReviewList() {

		let url = '';
		this.input_param.limit = this.limit;

		this.httpService.getHttp(url).subscribe(
			res => {
				//沒有資料 隱藏more data區塊
				if(res.products.length==0){
					console.log("empty array");
					this.moredata = 0;
				}

				this.list = res.products;
				console.log(res);
			}
		);

	}

}
