import { Component, OnInit } from '@angular/core';
import { LayoutService } from "../../services/layout.service";

@Component({
	selector: 'app-ebook-home',
	templateUrl: './ebook-home.component.html',
	styleUrls: ['./ebook-home.component.css']
})
export class EbookHomeComponent implements OnInit {

	input_param; 
	category_id = 0;
	sortType = 'post_date';
	order = 'desc';
	limit = 2;

	constructor(public layoutService:LayoutService) { }

	ngOnInit() {
		this.layoutService.getSummary('page_ebook_introduction');
		this.input_param = {
			website_id: 1,
			category_id: this.category_id,
			sort: this.sortType,
			sort_order: this.order,
			type: 'e-book',
			from: 0,
		};
		this.uiControl();
	}


	//sort 一般篩選器
	sort($type,$order){
		this.sortType = $type;
		this.order = $order;
		this.input_param = {
			website_id: 1,
			category_id: this.category_id,
			sort: $type,
			sort_order: $order,
			type: 'e-book',
			from: 0,
		};
	}


	//category component output
	getCategoryId($id){
		this.category_id = $id;
		this.input_param = {
			website_id: 1,
			category_id: $id,
			sort: 'post_date',
			sort_order: 'desc',
			type: 'e-book',
			from: 0,
		};
		//location.reload();
	}


	// ui jquery
	uiControl(){
		$(document).ready(function(){
			$('.nav-sort > a').click(function(){
				$('.nav-sort > a').removeClass('active');
				$(this).addClass('active');
			})
    });
	}

}
