import { Component, OnInit, OnChanges, SimpleChanges } from '@angular/core';
import { CookieService } from '../../services/cookie.service';
import { Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import * as Config from '../../models/config.model';
import { ProductComponent } from "../../share/product/product.component";

@Component({
	selector: 'app-course-bought',
	templateUrl: './course-bought.component.html',
	styleUrls: ['./course-bought.component.css']
})
export class CourseBoughtComponent extends ProductComponent {

	token;
	id;
	serverUrl = Config.APIUrl;

	//constructor(private cookieService:CookieService, private route:ActivatedRoute) { }

	
	ngOnInit() {
	//ngOnChanges(changes: SimpleChanges){
		this.route.params.subscribe(
			params => {
				this.getProduct(params['id']);
    			this.getSliders(params['id']);
				this.id = params['id'];
				this.token = this.cookieService.getCookie('token');
			}
		);
	}
	

}
