import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../services/http.service';
import * as Config from '../../models/config.model';
import { Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import { DomSanitizer } from '@angular/platform-browser';

@Component({
	selector: 'app-ebook-bought',
	templateUrl: './ebook-bought.component.html',
	styleUrls: ['./ebook-bought.component.css']
})
export class EbookBoughtComponent implements OnInit {

	link;
	constructor(private httpService:HttpService, private route:ActivatedRoute, public sanitizer: DomSanitizer) { }

	ngOnInit() {
		this.route.params.subscribe(
			param => {
				this.getBook(param['id']);
			}
		);
	}


	getBook(pid){
		let url = `${Config.APIUrl}/api/shop/my-product/e-book?id=${pid}`;
		this.httpService.getHttp(url).subscribe(
			res => this.link = res.link
		);
	}

}
